.header {
    width: 100%;
    min-height: 5vh;
    display: flex;
    justify-content: center;
    align-items: center;
   
  }
  
  .title {
    text-transform: uppercase;
    font-size: 10ch;
    text-align: center;
   
    padding: 20px ;
    color: #1c2833;

  }
  
  @media screen and (min-width: 800px) {
    .title {
        font-size: 10ch;

    }
  }
  @media screen  and (max-width: 700px){
    .title {
        font-size: 5ch;

    }   
  }